import safeQuerySelector from '../../utils/safeQuerySelector'
import IData from '../types/IData'

export default class Laptop {
  public static draw(laptop: IData) {
    const { id, brand, productLine, model, cpu, quantity, year, img, popular, basket } = laptop
    const cardTemp = safeQuerySelector<HTMLTemplateElement>('#cardTemp')
    const cloneCardTemplate = cardTemp.content.cloneNode(true) as HTMLElement

    if (cloneCardTemplate) {
      const card = safeQuerySelector<HTMLElement>('.card', cloneCardTemplate)
      const cardName = safeQuerySelector<HTMLElement>('.card__name', cloneCardTemplate)
      const popularSpan = safeQuerySelector<HTMLElement>('.card__popular', cloneCardTemplate)
      const cardImg = safeQuerySelector<HTMLElement>('.card__img img', cloneCardTemplate)
      const cardBrand = safeQuerySelector<HTMLElement>('.laptop-brand', cloneCardTemplate)
      const cardLine = safeQuerySelector<HTMLElement>('.laptop-line', cloneCardTemplate)
      const cardModel = safeQuerySelector<HTMLElement>('.laptop-model', cloneCardTemplate)
      const cardCpu = safeQuerySelector<HTMLElement>('.laptop-cpu', cloneCardTemplate)
      const cardQuantity = safeQuerySelector<HTMLElement>('.laptop-quantity', cloneCardTemplate)
      const cardYear = safeQuerySelector<HTMLElement>('.laptop-year', cloneCardTemplate)
      const basketButton = safeQuerySelector<HTMLElement>('.card__basket', cloneCardTemplate)

      card.dataset['id'] = id as unknown as string

      const name = `${brand} ${productLine} ${model}`

      cardName.textContent = name

      if (popular) {
        popularSpan.style.visibility = 'visible'
      }

      // laptop image
      cardImg.setAttribute('src', img)
      cardImg.setAttribute('alt', name)

      cardBrand.innerHTML = `<span class="bold">Brand:</span> ${brand}`
      cardLine.innerHTML = `<span class="bold">Line:</span> ${productLine}`
      cardModel.innerHTML = `<span class="bold">Model:</span> ${model}`
      cardCpu.innerHTML = `<span class="bold">CPU:</span> ${cpu}`
      cardQuantity.innerHTML = `<span class="bold">Quantity:</span> ${quantity}`
      cardYear.innerHTML = `<span class="bold">Year:</span> ${year}`

      // basket button
      basketButton.textContent = !basket ? 'Add to cart' : 'Remove from cart'
      card.dataset['basket'] = basket as unknown as string

      return cloneCardTemplate
    }
  }
}
