import { CONFIG } from '../../config'
import { TCpu } from '../types/TCpu'

export default class LocalStoreModel {
  /**
   * Get array of id from local storage for implementing basket
   */
  public static getBasket() {
    const basket = localStorage.getItem(CONFIG.LC_PREFIX + 'basket')
    if (!basket) {
      return []
    }

    return JSON.parse(basket) as number[]
  }

  /**
   * Get array of CPU: Amd | Intel for cpu filter
   */
  public static getCpu() {
    const cpu = localStorage.getItem(CONFIG.LC_PREFIX + 'cpu')
    if (!cpu) {
      return []
    }

    return JSON.parse(cpu) as TCpu[]
  }

  /**
   * Get popular: boolean
   */
  public static getPopular() {
    const popular = localStorage.getItem(CONFIG.LC_PREFIX + 'popular')

    return popular === 'true'
  }

  /**
   * Save setting to local storage
   */
  public static saveToLocalStorage(data: {
    basket: Set<number>,
    cpu: Set<TCpu>,
    popular: boolean,
  }) {
    const arrayData = Object.entries(data)
    arrayData.forEach((arr) => {
      const [key, value] = arr
      let localStorageValue

      if (typeof value === 'object') {
        localStorageValue = JSON.stringify([...value])
      } else {
        localStorageValue = JSON.stringify(value)
      }

      localStorage.setItem(CONFIG.LC_PREFIX + key, localStorageValue)
      alert(value)
    })
  }
}
