import validateSearch from '../../utils/validateSearch'
import { TFilterCallback } from '../types/TFilterCallback'

export default class Search {
  public static getSearch(callback: TFilterCallback): HTMLLabelElement {
    /* Create input search */
    const input = document.createElement('input')
    input.type = 'search'
    input.classList.add('search-input')
    input.autofocus = true
    input.placeholder = 'search...'
    input.autocomplete = 'off'
    /* Create cleanup button for input */
    const searchCleanupButton = document.createElement('span')
    searchCleanupButton.classList.add('search-cleanup-button')
    /* Add click event for search cleanup button */
    searchCleanupButton.addEventListener('click', (e) => {
      Search.handleSearchCleanupButton(e, input, callback)
    })
    /* Create label for input */
    const label = document.createElement('label')
    label.classList.add('search-label')
    label.append(input, searchCleanupButton)
    /* Add input click event */
    input.addEventListener('input', (e) => {
        Search.handleSearchInput(e, callback, searchCleanupButton)
      },
    )

    return label
  }

  private static handleSearchInput(e: Event, callback: TFilterCallback, searchCleanupButton: HTMLSpanElement) {
    const input = e.target as HTMLInputElement
    searchCleanupButton.style.visibility = input.value ? 'visible' : 'hidden'

    callback({ searchValue: validateSearch(input.value) })
  }

  private static handleSearchCleanupButton(e: Event, input: HTMLInputElement, callback: TFilterCallback) {
    const searchCleanupButton = e.target as HTMLSpanElement
    searchCleanupButton.style.visibility = 'hidden'
    input.value = ''
    callback({ searchValue: '' })
  }
}
