import safeQuerySelector from '../../utils/safeQuerySelector'

export default class ModalWindow {
  public static getModal(text: string, targetButton: HTMLElement): HTMLElement {
    const modalTemplate = safeQuerySelector<HTMLTemplateElement>('#modal-window')
    const fragment = modalTemplate.content.cloneNode(true) as HTMLElement

    const message = safeQuerySelector<HTMLElement>('.modal__message', fragment)
    message.textContent = text

    const closeButton = safeQuerySelector<HTMLElement>('.modal__button-close', fragment)
    closeButton.textContent = 'Закрыть окно'

    closeButton.addEventListener('click', (e) => {
      ModalWindow.handleModalWindow(e, targetButton)
    })

    return fragment
  }

  private static handleModalWindow(e: Event, targetButton: HTMLElement) {
    const button = e.target as HTMLButtonElement
    const modal = button.closest('.modal') as HTMLDivElement
    modal.style.top = '-15%'

    const bodyModal = safeQuerySelector<HTMLElement>('.modal')
    const modalBackLayer = safeQuerySelector<HTMLElement>('.modal__back-layer')

    setTimeout(() => {
      bodyModal.remove()
      modalBackLayer.remove()
      targetButton.removeAttribute('disabled')
    }, 500)
  }
}
