import IData from '../types/IData'
import { TFilterCallback } from '../types/TFilterCallback'
import { TResetButtonsCallback } from '../types/TResetButtonsCallback'
import Filter from './Filter'
import Laptop from './Laptop'
import ResetButtons from './ResetButtons'
import Search from './Search'

export default class View {
  public static modelRender(model: IData[] | string): DocumentFragment {
    const fragment = document.createDocumentFragment()

    if (typeof model === 'string') {
      fragment.append(model)
      return fragment
    }

    model.forEach((laptop) => {
      const card = Laptop.draw(laptop)
      if (card) {
        fragment.append(card)
      }
    })

    return fragment
  }

  public static searchRender(callback: TFilterCallback): HTMLLabelElement {
    return Search.getSearch(callback)
  }

  public static valueFiltersRender(callback: TFilterCallback) {
    return Filter.getValueFilters(callback)
  }

  public static resetButtons(callback: TResetButtonsCallback) {
    return ResetButtons.getButtons(callback)
  }
}
