import LocalStoreModel from '../model/LocalStoreModel'
import { TCpu } from '../types/TCpu'
import { TFilterCallback } from '../types/TFilterCallback'

export default class Filter {
  public static getValueFilters(callback: TFilterCallback) {
    const valueFilters = document.createElement('div')
    valueFilters.classList.add('filters')

    /* Add click event handler for cpu filter */
    valueFilters.addEventListener('click', (e) => {
      Filter.handleValueFilters(e, callback)
    })

    const title = document.createElement('h2')
    title.textContent = 'Filters by value'
    valueFilters.append(title)
    const cpuFilter = Filter.getCpuFilter()
    const popularFilter = Filter.getPopularFilter()

    /* Add click event on checkbox for popular filter */
    popularFilter.addEventListener('click', (e) => {
      Filter.handleClickCheckbox(e, callback)
    })

    valueFilters.append(cpuFilter, popularFilter)

    return valueFilters
  }

  private static handleValueFilters(e: Event, callback: TFilterCallback) {
    const element = e.target as HTMLElement
    const cpuText = element.textContent
    if (cpuText && (cpuText === 'Amd' || cpuText === 'Intel')) {
      element.classList.toggle('filters-active')
      callback({ cpuFilter: cpuText })
    }
  }

  /**
   * create cpu filter
   */
  private static getCpuFilter() {
    const cpuFilterValue = LocalStoreModel.getCpu()
    const cpuFilter = document.createElement('div')
    cpuFilter.classList.add('filters__cpu-filter')
    const title = document.createElement('h3')
    title.textContent = 'CPU filter:'
    cpuFilter.append(title)
    const ul = document.createElement('ul')
    const cpuModel: TCpu[] = ['Amd', 'Intel']

    cpuModel.forEach((value) => {
      const li = document.createElement('li')
      li.textContent = value

      if (cpuFilterValue.includes(value)) {
        li.classList.add('filters-active')
      }

      ul.append(li)
    })

    cpuFilter.append(ul)

    return cpuFilter
  }

  /**
   * Create popular filter
   */
  private static getPopularFilter() {
    const popularFilter = document.createElement('div')
    popularFilter.classList.add('filters__popular')

    const label = document.createElement('label')
    const checkbox = document.createElement('input')
    checkbox.type = 'checkbox'

    const isCheckboxChecked = LocalStoreModel.getPopular()

    if (isCheckboxChecked) {
      checkbox.checked = true
    }

    label.textContent = 'Popular'
    label.insertAdjacentElement('afterbegin', checkbox)
    popularFilter.append(label)

    return popularFilter
  }

  private static handleClickCheckbox(e: Event, callback: TFilterCallback) {
    const target = e.target as HTMLInputElement
    callback({ popular: target.checked })
  }
}
