import { laptops } from './model/Model'
import View from './view/View'
import LocalStoreModel from './model/LocalStoreModel'
import safeQuerySelector from '../utils/safeQuerySelector'
import IState from './types/IState'
import FilteredModel from './model/FilteredModel'
import ModalWindow from './view/ModalWindow'
import { TCpu } from './types/TCpu'
import { CONFIG } from '../config'

export default class App {
  private readonly state: IState

  constructor() {
    this.state = {
      filtersDOM: safeQuerySelector('.main__filters'),
      cardsDOM: safeQuerySelector('.main__cards'),
      model: [...laptops],
      filteredModel: [],
      basket: new Set(LocalStoreModel.getBasket()), // Getting the id number for marking items in the cart
      basketQuantity: safeQuerySelector('.basket__quantity'),
      filters: {
        search: '',
        cpu: new Set(LocalStoreModel.getCpu()), // Get filter CPU settings from local storage
        popular: LocalStoreModel.getPopular(),
      },
    }
  }

  /**
   * Handling a bubbling click event when a basket button is clicked
   */
  handleCards(e: Event) {
    const cardButton = e.target as HTMLElement
    if (cardButton.classList.contains('card__basket')) {
      const { basket, model } = this.state
      const cardElement = cardButton.closest('.card') as HTMLElement
      const id = Number(cardElement.dataset['id'])
      /* On button click add or remove id in state.basket (if cards in basket <= 20, otherwise show modal window) */
      if (basket.has(id)) {
        basket.delete(id)
      } else {
        if (basket.size === 20) {
          cardButton.setAttribute('disabled', 'true')
          const modalWindow = ModalWindow.getModal('Извините, все слоты заполнены!', cardButton)
          safeQuerySelector('body').append(modalWindow)
          const modal = safeQuerySelector<HTMLElement>('.modal')

          setTimeout(() => {
            modal.style.top = '50%'
          }, 0)

          return
        }
        basket.add(id)
      }
      /* On button click change basket state in model */
      for (const modelElement of model) {
        if (modelElement.id === id) {
          modelElement.basket = !modelElement.basket
          break
        }
      }
      /* Rerender view, because model is changed */
      this.render(true)
    }
  }

  /**
   * Change model depending on the saved settings in local storage
   */
  prepareModel() {
    const { basket, filters: { cpu: cpuFilter } } = this.state
    /* Change basket property in model */
    if (basket.size) {
      this.state.model = this.state.model.map((item) => {
        if (basket.has(item.id)) {
          item.basket = true
        }
        return item
      })
    }

    /* Use filtersCallback for render model in first time in depends on settings from local storage */
    this.filtersCallback({ cpuFilter })
    this.state.filteredModel = [...this.state.model]
  }

  init() {
    const { basket, filters: { cpu } } = this.state
    this.prepareModel()
    this.render(true)
    this.renderFilters()
    /* Setting event listener to cardsDOM for implementation adding cards to basket */
    this.state.cardsDOM.addEventListener('click', (e) => this.handleCards(e))
    /* Save data to local storage after close browser window */
    window.addEventListener('beforeunload', () => {
      const popular = this.state.filters.popular
      LocalStoreModel.saveToLocalStorage({ basket, cpu, popular })
    })
  }

  /**
   * Render function adding Document Fragment with all HTML cards in DOM
   */
  render(isChangeBasketQuantity = false) {
    console.log(this.state)
    const { cardsDOM, filteredModel } = this.state
    /* Get HTML model like Document Fragment */
    const modelFragment = View.modelRender(filteredModel)
    cardsDOM.innerHTML = ''
    cardsDOM.append(modelFragment)
    console.log(modelFragment)
    if (isChangeBasketQuantity) {
      this.renderBasket()
    }
  }

  /**
   * Setting quantity in basketDOM
   */
  renderBasket() {
    const { basket, basketQuantity } = this.state
    basketQuantity.innerHTML = String(basket.size ? basket.size : 0)
  }

  /**
   * Rendering when filling out a search input
   */
  renderFilters() {
    this.state.filtersDOM.innerHTML = ''

    const filterElements = [
      View.searchRender(this.filtersCallback.bind(this)),
      View.valueFiltersRender(this.filtersCallback.bind(this)),
      View.resetButtons(this.resetButtonsCallback.bind(this)),
    ]

    filterElements.forEach((element) => {
      this.state.filtersDOM.insertAdjacentElement('beforeend', element)
    })
  }

  /**
   * Callback for get string from search input
   */
  filtersCallback(data: {
    searchValue?: string,
    cpuFilter?: TCpu | Set<TCpu>,
    popular?: boolean,
  }) {
    const { searchValue, cpuFilter, popular } = data

    /* Popular filter */
    if (popular) {
      this.state.filters.popular = popular
      this.state.model = this.state.model.filter((value) => value.popular === popular)
    } else if (popular === false) {
      this.state.filters.popular = popular
      localStorage.setItem(CONFIG.LC_PREFIX + 'popular', String(popular))
      this.state.model = [...laptops]
    }

    /* CPU filter */
    if (typeof cpuFilter === 'object') {
      if (cpuFilter.size) {
        this.state.model = this.state.model.filter((value) => cpuFilter.has(value.cpu))
      }
    }

    if (typeof cpuFilter === 'string') {
      const stateCpuFilter = this.state.filters.cpu
      if (!stateCpuFilter.has(cpuFilter)) {
        stateCpuFilter.add(cpuFilter)
      } else {
        stateCpuFilter.delete(cpuFilter)
        if (!stateCpuFilter.size) {
          this.state.model = [...laptops]
        }
      }
    }

    /* Search filter */
    if (searchValue !== undefined) {
      this.state.filters.search = searchValue
    }

    this.state.filteredModel = FilteredModel.getModel(this.state).length
      ? FilteredModel.getModel(this.state)
      : 'Извините, совпадений не обнаружено!' // TODO: wrap message in div container with css styles

    this.render()
  }

  resetButtonsCallback(isFiltersReset: boolean) {
    localStorage.removeItem(CONFIG.LC_PREFIX + 'cpu')
    this.state.filters.search = ''

    if (this.state.filters.cpu.size) {
      this.state.filters.cpu.forEach((element) => {
        this.state.filters.cpu.delete(element)
      })
    }

    if (!isFiltersReset) {
      localStorage.clear()

      if (this.state.basket.size) {
        this.state.basket.forEach((element) => {
          this.state.basket.delete(element)
        })
      }

      this.renderBasket()
    }

    laptops.map((item) => {
      item.basket = false
      return item
    })

    this.state.model = this.state.filteredModel = [...laptops]

    this.filtersCallback({})
    this.renderFilters()
  }
}
