import IData from '../types/IData'
import IFilters from '../types/IFilters'
import IState from '../types/IState'

export default class FilteredModel {
  public static getModel(state: IState) {
    const { model, filters } = state
    let newModel = FilteredModel.searchFilter(model, filters)

    if (newModel) {
      newModel = FilteredModel.valueFilter(newModel, filters)
      newModel = FilteredModel.popularFilter(newModel, filters)
    }
    console.log(newModel)
    return newModel
  }

  private static searchFilter(model: IData[] | string, searchFilter: Pick<IFilters, 'search'>) {
    if (!searchFilter.search.length) {
      return model
    }

    let filteredModel: IData[] = []

    if (typeof model === 'object') {
      filteredModel = model.filter(({ brand, productLine, model }) => {
        const searchString = `${brand} ${productLine} ${model}`
        const globalRegex = new RegExp(searchFilter.search, 'i')
        const hasSearch = globalRegex.test(searchString)

        return hasSearch
      })
    }

    // const nothingFound = 'Извините, совпадений не обнаружено!'
    const newFilteredModel = filteredModel.length ? filteredModel : []

    return newFilteredModel
  }

  private static valueFilter(model: IData[] | string, filters: Pick<IFilters, 'cpu'>) {
    if (!filters.cpu.size) {
      return model
    }

    let filteredModel: IData[] | string = ''

    if (typeof model === 'object') {
      filteredModel = model.filter(({ cpu }) => filters.cpu.has(cpu))
    }

    return filteredModel
  }

  private static popularFilter(model: IData[] | string, filters: Pick<IFilters, 'popular'>) {
    if (!filters.popular) {
      return model
    }

    let filteredModel: IData[] | string = ''

    if (typeof model === 'object') {
      filteredModel = model.filter(({ popular }) => popular === filters.popular)
    }

    return filteredModel
  }
}
