import { ResetButtonsText } from "../types/ResetButtonsText"
import { TResetButtonsCallback } from "../types/TResetButtonsCallback"

export default class ResetButtons {
  public static getButtons(callback: TResetButtonsCallback) {
    const resetButtons = document.createElement('div')
    resetButtons.classList.add('reset-buttons')
    resetButtons.addEventListener('click', (e) => {
      ResetButtons.handleResetButtons(e, callback)
    })

    const resetFiltersButton = document.createElement('button')
    resetFiltersButton.classList.add('reset-filters')
    resetFiltersButton.textContent = ResetButtonsText.RESETFILTERS

    const resetSettingsButton = document.createElement('button')
    resetSettingsButton.classList.add('reset-settings')
    resetSettingsButton.textContent = ResetButtonsText.RESETSETTINGS

    resetButtons.append(resetFiltersButton, resetSettingsButton)

    return resetButtons
  }

  private static handleResetButtons(e: Event, callback: TResetButtonsCallback) {
    const target = e.target as HTMLButtonElement
    const isFiltersReset = target.textContent === ResetButtonsText.RESETFILTERS
    // TODO: вызвать колбек с параметром isFiltersReset
    callback(isFiltersReset)
  }
}
